

















import { Component, Vue } from 'vue-property-decorator';
import { Action, State } from 'vuex-class';
import { IArticle, IListInput } from '@/types/interface';
import Banner from '../../components/Banner.vue';
import ArticleListItem from '../../components/ArticleListItem.vue';
import pagination from '../../components/Pagination.vue';

@Component({
  components: {
    Banner,
    ArticleListItem,
    pagination,
  },
})

export default class ArticlePage extends Vue {
  @State('articleTag') stateTag!: string;
  @State('articleList', { namespace: 'article' }) stateArticleList!: IArticle[];
  @Action('saveArticleTag') saveArticleTag!: (t: string) => void;
  @Action('getArticleList', { namespace: 'article' }) getArticleList!: (params: IListInput) => void;
  @Action('getArticleSimple', { namespace: 'article' }) getArticleSimple!: (id: number) => Promise<void>;

  pageSplitNum = 4;
  pageIdx = 1;

  link() {
    this.$router.push('/main');
    this.saveArticleTag('');
  }

  tagSelected = '';

  get listFilter() {
    return this.stateTag === '' ? this.stateArticleList
      : this.stateArticleList.filter((d) => d.hashtags.indexOf(this.stateTag) >= 0);
  }

  get sliceList() {
    return this.listFilter.slice((this.pageIdx - 1) * this.pageSplitNum, this.pageIdx * this.pageSplitNum);
  }

  get pages() {
    return Math.ceil(this.listFilter.length / this.pageSplitNum);
  }

  created() {
    this.getArticleList({
      count: '20',
      // eslint-disable-next-line @typescript-eslint/camelcase
      search_key: '',
      keyword: '',
      days: '400',
    });
  }

  show(id: number) {
    this.getArticleSimple(id)
    .then(() => {
      this.$router.push({ path: '/main/article_content', query: { id: id.toString() } });
    });
  }

  tag(t: string) {
    this.saveArticleTag(t);
  }

  changeIdx(i: number) {
    this.pageIdx = i;
  }
}
