



















import {
  Component, Vue, Prop, Emit,
} from 'vue-property-decorator';
import { IArticle } from '@/types/interface';

@Component
export default class ArticleListItem extends Vue {
  @Prop() item!: IArticle;

  @Emit()
  select(id: number) {
    return id;
  }

  @Emit()
  tagHandler(t: string) {
    return t;
  }
}
